"use client";
import { useState, useEffect } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import axios from "axios";

export default function Newuser() {
  const router = useRouter();
  const searchParams = useSearchParams();

  const email = searchParams.get("email") || "";
  const password = searchParams.get("password") || "";

  const [form, setForm] = useState({ email, password, fname: "", lname: "" });
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setForm({ email, password, fname: "", lname: "" });
  }, [email, password]);

  // Handle input change
  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const response = await axios.post("http://localhost:5000/api/create", form);

      if (response.status === 201) {
        alert("User created successfully!");
        router.push("/dashboard"); // Redirect after successful registration
      }
    } catch (error) {
      setError("Error adding user. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex justify-center items-center h-screen bg-gray-100">
      <div className="w-1/3 bg-white p-6 rounded-lg shadow-lg">
        <h2 className="text-2xl font-bold mb-4 text-center">Complete Your Profile</h2>
        {error && <p className="text-red-500 text-center">{error}</p>}
        <form onSubmit={handleSubmit}>
          <div className="mb-3">
            <label className="block font-semibold">Email</label>
            <input type="email" name="email" value={form.email} readOnly className="w-full p-2 border rounded bg-gray-200" />
          </div>
          <div className="mb-3">
            <label className="block font-semibold">Password</label>
            <input type="password" name="password" value={form.password} readOnly className="w-full p-2 border rounded bg-gray-200" />
          </div>
          <div className="mb-3">
            <label className="block font-semibold">First Name</label>
            <input type="text" name="fname" value={form.fname} onChange={handleChange} className="w-full p-2 border rounded" required />
          </div>
          <div className="mb-3">
            <label className="block font-semibold">Last Name</label>
            <input type="text" name="lname" value={form.lname} onChange={handleChange} className="w-full p-2 border rounded" required />
          </div>
          <button type="submit" className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600 w-full" disabled={loading}>
            {loading ? "Adding User..." : "Add User"}
          </button>
        </form>
      </div>
    </div>
  );
}
